.header {
    background: var(--background);
    border-bottom: 1px solid var(--border);

    position: fixed;
    top: 0;
    width: 100%;
}

.header .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    height: 60px;

    max-width: 957px;
    margin: 0 auto;
    width: 90%;
}

.header .container .logo {
    width:110px;
    cursor: pointer;
}

.inputFake{
    background: var(--background-main);
    border-radius: 3px;

    padding: 3px 10px;

    border:  1px solid var(--border);
    display: flex;
    align-items: center;
}

.inputFake input {
    font-size: 12px;
    padding: 0px 7px;
}
.menu-icons{
    display: flex;
    align-items: center;
    gap: 16px;
    cursor: pointer;
}

.menu-icons .imgUser{
    width: 26px;
    height: 26px;
    border-radius: 100%;
}